export default {
	data() {
		return {
			source: 0,
			addressList: []
		}
	},
	onShow() {
		if(uni.getStorageSync('cbe_token')){
			this.token = uni.getStorageSync('cbe_token');
			this.getAddressList();
		}else{
			uni.showToast({ title:"未登录或无权限",icon: "none"});
			var timer = setTimeout(()=>{
				uni.navigateBack({
					delta:1
				})
			},1000);
		}
	},
	onLoad(option){
		if(option.source){
			this.source = option.source;
		}
	},
	methods: {
		//选择地址
		checkAddress(item){
			if(this.source == 1){
				this.$api.prePage().addressData = item;
				uni.navigateBack()
			}
		},
		del(id){
			var that = this;
			uni.showModal({
				title: '温馨提示',
				confirmText:'删除',
				content: "确定要删除该地址信息吗？",
				success: function (res) {
					if(res.confirm) {
						uni.showLoading({
							title: '正在处理',
							mask: true
						});
						uni.request({
							url:uni.getStorageSync('hostname') + '?action=index/member/addressDel',
							data: {token:uni.getStorageSync('cbe_token'),id:id},
							method:'POST',
							dataType:'json',
							header: {'content-type' : 'application/x-www-form-urlencoded'},
							success: (resa) => {
								uni.hideLoading();
								var one = resa.data;
								if(one.code==0){
									uni.showToast({
										title:one.msg,icon:'success',mask:true,duration: 3000
									});
									that.getAddressList();
								}else{
									uni.showToast({ title:one.msg, icon: "none" });
								}
							}
						})
					}
				}
			});
		},
		moren(id,mm,index){
			if(mm==1){
				return false;
			}
			var that = this;
			uni.showModal({
				title: '温馨提示',
				confirmText:'设置',
				content: "确定要设置该地址为默认地址吗？",
				success: function (res) {
					if(res.confirm) {
						uni.showLoading({
							title: '正在处理',
							mask: true
						});
						uni.request({
							url:uni.getStorageSync('hostname') + '?action=index/member/addressMoren',
							data: {token:uni.getStorageSync('cbe_token'),id:id},
							method:'POST',
							dataType:'json',
							header: {'content-type' : 'application/x-www-form-urlencoded'},
							success: (resa) => {
								uni.hideLoading();
								var one = resa.data;
								if(one.code==0){
									uni.showToast({
										title:one.msg,icon:'success',mask:true,duration: 3000
									});
									that.addressList = [];
									that.getAddressList();
								}else{
									uni.showToast({ title:one.msg, icon: "none" });
								}
							}
						})
					}
				}
			});
		},
		fahuiaa(aa,cc){
			var bb = JSON.parse(aa);
			var dd = JSON.parse(cc);
			this.sji().addressData = bb;
			this.sji().goumaiData  = dd;
			uni.navigateBack({
				delta:1
			})
		},
		sji(){
			let pages = getCurrentPages();
			let prePage = pages[pages.length - 3];



			return prePage.$vm;
		},
		addAddress(type, item){
			if(this.source==0){
				uni.navigateTo({
					url: `/pages/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
				})
			}else{
				uni.navigateTo({
					url: `/pages/address/addressManage?source=1&type=${type}&data=${JSON.stringify(item)}`
				})
			}
		},
		getAddressList(){
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/member/addressList',
				data: {token:uni.getStorageSync('cbe_token')},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					var one = resa.data;
					if(one.code==0){
						this.addressList = one.data;
					}
				}
			})
		}
	}
}
